@charset "UTF-8";
.accordion {
  width: 80%;
  margin: 0 auto;
}

.accordion .accordion_outer {
  padding: 10px;
  margin-top: 15px auto 0 auto;
  cursor: pointer;
  position: relative;
  display: flex;
}

.accordion .accordion_outer .icon_q {
  font-size: 3rem;
  color: white;
  padding: 5px 5px;
  margin: 10px 0;
  background-color: #9e0e0e;
  font-weight: bold;
  display: inline-block;
  box-sizing: border-box;
}

.accordion .accordion_outer .ques {
  font-size: 2rem;
  padding: 5px 5px;
  margin: 10px 0;
  color: white;
  background-color: #fc4c07;
  box-sizing: border-box;
}

.accordion .accordion_inner {
  padding: 10px;
  background-color: white;
  box-shadow: 5px 5px 5px #969494;
  position: relative;
  display: flex;
}

.accordion .accordion_inner .icon_a {
  font-size: 2.2rem;
  color: black;
  padding: 5px 5px;
  margin: 10px 0;
  background-color: yellow;
  font-weight: bold;
  box-sizing: border-box;
  display: inline-block;
  text-align: center;
}

.accordion .accordion_inner .answer {
  font-size: 1.4rem;
  margin: 0 auto;
  max-width: 90%;
  color: white;
  color: black;
}

.accordion .grad-wrap + .grad-wrap {
  margin-top: 40px;
}

.accordion .grad-trigger.fa {
  margin-right: .5em;
}

.accordion .grad-item {
  position: relative;
  overflow: hidden;
  display: flex;
}

.accordion .grad-item.is-hide {
  height: 40px;
}

.accordion .grad-item::before {
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 40px;
  background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.9) 50%);
  background: linear-gradient(top, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.9) 50%);
}

.accordion .grad-trigger.is-show {
  bottom: -2em;
}

.accordion .grad-trigger.is-show::after {
  content: "閉じる";
}

.accordion .grad-trigger.is-show + .grad-item::before {
  display: none;
}
